import {
  Component,
  AfterViewInit,
  OnDestroy,
  ViewChild,
  ElementRef,
  ChangeDetectorRef,OnInit
} from '@angular/core';
import { NgForm } from '@angular/forms';
import { CartDataService } from '../services/cart-data.service';
declare var Stripe:any;
@Component({
  selector: 'app-stripe-payement',
  templateUrl: './stripe-payement.component.html',
  styleUrls: ['./stripe-payement.component.scss']
})
export class StripePayementComponent implements AfterViewInit, OnDestroy {
  total:number;
  constructor(private cd: ChangeDetectorRef,private cartService:CartDataService) {}

  ngOnInit() {
    this.cartService.isTotalPrice.subscribe(value =>{
      this.total = value;
    })
  }
  @ViewChild('cardInfo') cardInfo: ElementRef;

  card: any;
  cardHandler = this.onChange.bind(this);
  error: string;

  

  ngAfterViewInit() {
    var stripe = Stripe('pk_test_9pF6IgWVPFnh5ZowHn7HaBuH'); // use your test publishable key
    var elements = stripe.elements();

     const style = {
    base: {
      lineHeight: '24px',
      fontFamily: 'monospace',
      fontSmoothing: 'antialiased',
      fontSize: '19px',
      '::placeholder': {
        color: 'purple'
      }
    }
  };

  this.card = elements.create('card', { style });
  this.card.mount(this.cardInfo.nativeElement);

  this.card.addEventListener('change', this.cardHandler);
  }

  ngOnDestroy() {
    this.card.removeEventListener('change', this.cardHandler);
    this.card.destroy();
  }

  onChange({ error }) {
    if (error) {
      this.error = error.message;
    } else {
      this.error = null;
    }
    this.cd.detectChanges();
  }

  async onSubmit(form: NgForm) {
    const { token, error } = await stripe.createToken(this.card);

    if (error) {
      console.log('Something is wrong:', error);
    } else {
      console.log('Success!', token);
      // ...send the token to the your backend to process the charge
    }
  }
 
}
